    <view v-if="merchantStatus.hasPendingApply" class="status-card pending">
      <text class="status-icon">‚è≥</text>
      <view class="status-content">
        <text class="status-title">Áî≥ËØ∑ÂÆ°Ê†∏‰∏≠</text>
        <text class="status-desc">ÊÇ®ÁöÑÂïÜÂÆ∂Áî≥ËØ∑Ê≠£Âú®ÂÆ°Ê†∏‰∏≠ÔºåËØ∑ËÄêÂøÉÁ≠âÂæÖ</text>
      </view>
    </view>
    
    <view v-else-if="merchantStatus.isMerchant" class="status-card success">
      <text class="status-icon">‚úÖ</text>
      <view class="status-content">
        <text class="status-title">ÊÇ®Â∑≤ÊòØÂïÜÂÆ∂</text>
        <text class="status-desc">ÂèØ‰ª•Âú®‰ªªÂä°Â§ßÂéÖÊé•ÂçïÂï¶</text>
      </view>
    </view>
    
    <!-- Áî≥ËØ∑Ë°®Âçï -->
    <view v-else class="form-container">
      <!-- ÂïÜÊà∑Á±ªÂûãÈÄâÊã© -->
      <view class="section-title">ÂïÜÊà∑Á±ªÂûã</view>
      <view class="type-cards">
        <view 
          class="type-card" 
          :class="{ active: form.merchantType === 1 }"
          @click="form.merchantType = 1"
        >
          <text class="type-icon">üë§</text>
          <text class="type-name">‰∏™‰ΩìÂ∑•ÂïÜÊà∑</text>
          <text class="type-rate">ÊèêÊàê 8%</text>
        </view>
        <view 
          class="type-card" 
          :class="{ active: form.merchantType === 2 }"
          @click="form.merchantType = 2"
        >
          <text class="type-icon">üè¢</text>
          <text class="type-name">‰ºÅ‰∏ö/ÁªÑÁªá</text>
          <text class="type-rate">ÊèêÊàê 5%</text>
        </view>
      </view>
      
      <!-- Âü∫Êú¨‰ø°ÊÅØ -->
      <view class="section-title">Âü∫Êú¨‰ø°ÊÅØ</view>
      <view class="form-card">
        <view class="form-item">
          <text class="label required">Â∫óÈì∫ÂêçÁß∞</text>
          <input v-model="form.shopName" class="input" placeholder="ËØ∑ËæìÂÖ•Â∫óÈì∫ÂêçÁß∞" />
        </view>
        
        <view v-if="form.merchantType === 2" class="form-item">
          <text class="label required">‰ºÅ‰∏öÂêçÁß∞</text>
          <input v-model="form.companyName" class="input" placeholder="ËØ∑ËæìÂÖ•‰ºÅ‰∏öÂÖ®Áß∞" />
        </view>
        
        <view class="form-item">
          <text class="label required">Ê≥ï‰∫∫/Ë¥üË¥£‰∫∫</text>
          <input v-model="form.legalPerson" class="input" placeholder="ËØ∑ËæìÂÖ•ÂßìÂêç" />
        </view>
        
        <view class="form-item">
          <text class="label required">Ë∫´‰ªΩËØÅÂè∑</text>
          <input v-model="form.legalIdCard" class="input" placeholder="ËØ∑ËæìÂÖ•Ë∫´‰ªΩËØÅÂè∑" />
        </view>
        
        <view class="form-item">
          <text class="label required">ËÅîÁ≥ªÁîµËØù</text>
          <input v-model="form.contactPhone" class="input" type="number" placeholder="ËØ∑ËæìÂÖ•ËÅîÁ≥ªÁîµËØù" />
        </view>
        
        <view class="form-item">
          <text class="label">ËÅîÁ≥ªÈÇÆÁÆ±</text>
          <input v-model="form.contactEmail" class="input" type="email" placeholder="ËØ∑ËæìÂÖ•ÈÇÆÁÆ±" />
        </view>
        
        <view class="form-item">
          <text class="label">ÁªèËê•Âú∞ÂùÄ</text>
          <input v-model="form.businessAddress" class="input" placeholder="ËØ∑ËæìÂÖ•ÁªèËê•Âú∞ÂùÄ" />
        </view>
      </view>
      
      <!-- ËµÑË¥®ÊùêÊñô -->
      <view class="section-title">ËµÑË¥®ÊùêÊñô</view>
      <view class="form-card">
        <view class="upload-item">
          <text class="label required">Ë∫´‰ªΩËØÅÊ≠£Èù¢</text>
          <view class="upload-box" @click="uploadImage('idCardFront')">
            <image v-if="form.idCardFront" :src="form.idCardFront" class="upload-img" mode="aspectFill" />
            <view v-else class="upload-placeholder">
              <text class="iconfont icon-add"></text>
              <text class="upload-text">‰∏ä‰º†ÁÖßÁâá</text>
            </view>
          </view>
        </view>
        
        <view class="upload-item">
          <text class="label required">Ë∫´‰ªΩËØÅÂèçÈù¢</text>
          <view class="upload-box" @click="uploadImage('idCardBack')">
            <image v-if="form.idCardBack" :src="form.idCardBack" class="upload-img" mode="aspectFill" />
            <view v-else class="upload-placeholder">
              <text class="iconfont icon-add"></text>
              <text class="upload-text">‰∏ä‰º†ÁÖßÁâá</text>
            </view>
          </view>
        </view>
        
        <view class="upload-item">
          <text class="label" :class="{ required: form.merchantType === 2 }">Ëê•‰∏öÊâßÁÖß</text>
          <view class="upload-box" @click="uploadImage('licenseImg')">
            <image v-if="form.licenseImg" :src="form.licenseImg" class="upload-img" mode="aspectFill" />
            <view v-else class="upload-placeholder">
              <text class="iconfont icon-add"></text>
              <text class="upload-text">‰∏ä‰º†ÁÖßÁâá</text>
            </view>
          </view>
        </view>
        
        <view v-if="form.merchantType === 2" class="form-item">
          <text class="label">Ëê•‰∏öÊâßÁÖßÂè∑</text>
          <input v-model="form.licenseNo" class="input" placeholder="ËØ∑ËæìÂÖ•Ëê•‰∏öÊâßÁÖßÂè∑" />
        </view>
      </view>
      
      <!-- Êèê‰∫§ÊåâÈíÆ -->
      <view class="btn-group">
        <view class="submit-btn" @click="handleSubmit">Êèê‰∫§Áî≥ËØ∑</view>
      </view>
    </view>
  </view>
</template>

<script>
import { request } from '@/utils/request'
import { useUserStore } from '@/stores/user'

export default {
  data() {
    return {
      merchantStatus: {
        isMerchant: false,
        hasPendingApply: false
      },
      form: {
        merchantType: 1,
        shopName: '',
        companyName: '',
        legalPerson: '',
        legalIdCard: '',
        contactPhone: '',
        contactEmail: '',
        businessAddress: '',
        licenseNo: '',
        idCardFront: '',
        idCardBack: '',
        licenseImg: ''
      },
      uploading: false
    }
  },
  mounted() {
    this.loadMerchantStatus()
  },
  methods: {
    async loadMerchantStatus() {
      try {
        const res = await request({
          url: '/merchant/status',
          method: 'GET'
        })
        if (res.data) {
          this.merchantStatus = res.data
          
          // Â¶ÇÊûúÂ∑≤ÁªèÊòØÂïÜÂÆ∂ÔºåÊèêÁ§∫Âπ∂ËøîÂõû
          if (res.data.isMerchant) {
            setTimeout(() => {
              uni.navigateBack()
            }, 2000)
          }
        }
      } catch (e) {
        console.error('Âä†ËΩΩÂïÜÂÆ∂Áä∂ÊÄÅÂ§±Ë¥•', e)
      }
    },
    uploadImage(field) {
      uni.chooseImage({
        count: 1,
        sizeType: ['compressed'],
        sourceType: ['album', 'camera'],
        success: (res) => {
          const tempFilePath = res.tempFilePaths[0]
          this.doUpload(tempFilePath, field)
        }
      })
    },
    async doUpload(filePath, field) {
      this.uploading = true
      uni.showLoading({ title: '‰∏ä‰º†‰∏≠...' })
      
      try {
        const userStore = useUserStore()
        const token = userStore.token
        
        if (!token) {
          uni.hideLoading()
          uni.showToast({ title: 'ËØ∑ÂÖàÁôªÂΩï', icon: 'none' })
          setTimeout(() => {
            uni.navigateTo({ url: '/pages/login/index' })
          }, 1500)
          return
        }
        
        uni.uploadFile({
          url: 'http://localhost:8080/api/file/upload',
          filePath: filePath,
          name: 'file',
          formData: {
            folder: 'images'
          },
          header: {
            'Authorization': token
          },
          success: (uploadRes) => {
            const data = JSON.parse(uploadRes.data)
            if (data.code === 200) {
              this.form[field] = data.data.url
              uni.showToast({ title: '‰∏ä‰º†ÊàêÂäü', icon: 'success' })
            } else {
              uni.showToast({ title: data.message || '‰∏ä‰º†Â§±Ë¥•', icon: 'none' })
            }
          },
          fail: (err) => {
            console.error('‰∏ä‰º†Â§±Ë¥•', err)
            uni.showToast({ title: '‰∏ä‰º†Â§±Ë¥•', icon: 'none' })
          },
          complete: () => {
            uni.hideLoading()
            this.uploading = false
          }
        })
      } catch (e) {
        uni.hideLoading()
        this.uploading = false
        console.error('‰∏ä‰º†Â§±Ë¥•', e)
      }
    },
    async handleSubmit() {
      // È™åËØÅÂøÖÂ°´È°π
      if (!this.form.shopName) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•Â∫óÈì∫ÂêçÁß∞', icon: 'none' })
        return
      }
      if (this.form.merchantType === 2 && !this.form.companyName) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•‰ºÅ‰∏öÂêçÁß∞', icon: 'none' })
        return
      }
      if (!this.form.legalPerson) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•Ê≥ï‰∫∫/Ë¥üË¥£‰∫∫ÂßìÂêç', icon: 'none' })
        return
      }
      if (!this.form.legalIdCard) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•Ë∫´‰ªΩËØÅÂè∑', icon: 'none' })
        return
      }
      if (!this.form.contactPhone) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ËÅîÁ≥ªÁîµËØù', icon: 'none' })
        return
      }
      if (!this.form.idCardFront) {
        uni.showToast({ title: 'ËØ∑‰∏ä‰º†Ë∫´‰ªΩËØÅÊ≠£Èù¢', icon: 'none' })
        return
      }
      if (!this.form.idCardBack) {
        uni.showToast({ title: 'ËØ∑‰∏ä‰º†Ë∫´‰ªΩËØÅÂèçÈù¢', icon: 'none' })
        return
      }
      if (this.form.merchantType === 2 && !this.form.licenseImg) {
        uni.showToast({ title: 'ËØ∑‰∏ä‰º†Ëê•‰∏öÊâßÁÖß', icon: 'none' })
        return
      }
      
      uni.showLoading({ title: 'Êèê‰∫§‰∏≠...' })
      try {
        await request({
          url: '/merchant/apply',
          method: 'POST',
          data: this.form
        })
        
        uni.hideLoading()
        uni.showModal({
          title: 'Êèê‰∫§ÊàêÂäü',
          content: 'ÊÇ®ÁöÑÂïÜÂÆ∂Áî≥ËØ∑Â∑≤Êèê‰∫§ÔºåËØ∑Á≠âÂæÖÂÆ°Ê†∏',
          showCancel: false,
          success: () => {
            // Ëß¶ÂèëÁî®Êà∑‰∏≠ÂøÉÈ°µÈù¢Âà∑Êñ∞
            const pages = getCurrentPages()
            if (pages.length > 1) {
              const prevPage = pages[pages.length - 2]
              // Â¶ÇÊûú‰∏ä‰∏ÄÈ°µÊòØÁî®Êà∑‰∏≠ÂøÉÈ°µÈù¢ÔºåËß¶ÂèëÂÖ∂Âà∑Êñ∞ÊñπÊ≥ï
              if (prevPage.route === 'pages/user/index' && prevPage.$vm.loadMerchantStatus) {
                prevPage.$vm.loadMerchantStatus()
              }
            }
            uni.navigateBack()
          }
        })
      } catch (e) {
        uni.hideLoading()
        uni.showToast({ title: e.message || 'Êèê‰∫§Â§±Ë¥•', icon: 'none' })
      }
    }
  }
}
</script>

